//
// Utility classes
// --------------------------------------------------


// Floats
// -------------------------

.clearfix {
  .clearfix();
}

.center-block {
  .center-block();
}

.pull-right {
  float: right !important;
}

.pull-left {
  float: left !important;
}


// Toggling content
// -------------------------

// Note: Deprecated .hide in favor of .hidden or .sr-only (as appropriate) in v3.0.1
.hide {
  display: none !important;
}

.show {
  display: block !important;
}

.invisible {
  visibility: hidden;
}

.text-hide {
  .text-hide();
}


// Hide from screenreaders and browsers
//
// Credit: HTML5 Boilerplate

.hidden {
  display: none !important;
}

.custom-padding-top {
  padding-top: 18px;
  @media (max-width: @screen-xs-max) { 
    padding-top: 0px;
  }
}

.custom-icon {
  margin-right: -5px;
  margin-left: -7px;
}


// For Affix plugin
// -------------------------

.affix {
  position: fixed;
}

// Margins
// -------------------------

.with-margin {
  margin: @margin-size;
}

.with-margin-vertical {
  margin-top: calc(@margin-size + 5px);
  margin-bottom: @margin-size;
}

.with-margin-right {
  margin-right: @margin-size;
}

.with-margin-left {
  margin-left: @margin-size;
}

.with-margin-top {
  margin-top: @margin-size;
}

.with-margin-bottom {
  margin-bottom: @margin-size;
}

.no-padding {
  padding: 0px !important;
}

.no-margin {
  margin: 0px !important;
}


// Borders
// -------------------------

.with-border-bottom {
  border-bottom: solid 1px @border-color;
}

.line-divider {
  border-top: 1px solid @border-color;
}

.with-shadow {
  box-shadow: 0px 9px 18px 5px rgba(0,0,0,0.23);
}

.news-item {
  padding-top: @news-padding-vertical;
  padding-bottom: @news-padding-vertical;
  a {
    &:hover {
      text-decoration: none;
      color: @brand-info;
    }
  }
  .news-date {  
    font-size: @font-size-description;
    color: @box-date-color;
  }
  .news-title {
    font-weight: 500;
    font-size: @font-size-header-links;
    color: @box-color;
    &:hover{
      color: @navbar-default-link-color;
    }
  }
  .news-description {
    color: @box-color;
    font-size: @font-size-description;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    @media (max-width: @screen-xs-max) { 
      -webkit-line-clamp: 2;
    }
  }
  .news-image {
    height: @news-image;
    img {
      height: 100%;
      width: auto;
      max-width: initial;
      min-width: 100%;
      -webkit-transform: translate(-50%, -50%);
      transform: translate(-50%, -50%);
      position: absolute;
      left: 50%;
      top: 50%;
      padding-left: 15px;
      padding-right: 15px;
    }
    @media (max-width: @screen-xs-max) { 
      height: 19vw;
    }
  }
  .news-content {
    padding-left: 25px;
  }
}

.button-container {
  margin-top: calc(@margin-size * 2);
}

.custom-tab {
  padding-right: calc(@margin-size * 5);
  @media (max-width: @screen-md-min) { 
    padding-right: 15px;
  }
}

.logos {
  img {
    height: @logo-height;
    max-height: @logo-height;
    margin: 0 auto;
  }
}

.certification-container {
  padding: @margin-size;
}

// Backgrounds

.full-screen {
  height: 100%;
}

.bg-primary {
  background-color: @brand-primary;
}

.bg-info {
  background-color: @brand-info;
}

.centered {
  position: absolute;
  top: 50%;
  left: 50%;
  margin: 0;
  transform: translate(-50%,-49%);
}

